@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
  color: #3f3f3f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
}

.FitnessTracking {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.FitnessTable {
    width: 100%;
    margin-top: 40px;
    border-collapse: collapse;
    border-radius: 20px;
}

.FitnessTable > thead > tr > th {
    padding: 20px 0 30px 0;
    font-size: 1rem;
    font-weight: normal;
}

.TableBody {
    box-shadow: 0 0 0 1px #7f7f7f;
    border-radius: 20px;

}

.rowTable > th {
    padding: 10px;
}

.FitnessTableButton {
    border: none;
    margin-left: 15px;
    font-size: 1.5rem;
    cursor: pointer;
}

.Form {
    display: flex;
    align-items: flex-end;
}

.label {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
}

.label:first-child {
    margin-left: 0;
}

.FormInput {
    padding: 8px;
    outline: none;
    border: 1px solid #7f7f7f;
    border-radius: 8px;
    margin-top: 15px;
}

.FormButtonSubmit {
    border: 1px solid #7f7f7f;
    border-radius: 8px;
    padding: 8px 15px;
    margin-left: 30px;
    cursor: pointer;
}

